html {
  height: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  // min-height: 100vh;
  // min-width: 100vh;
  font-family: "Barlow", "Arial", "sans-serif";
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  background-color: #252836;
}

.app {
  display: flex;
}